.container {
  height: auto;
  width: 100%;
  display: grid;
  position: absolute;
  grid-template-columns: 1fr 1fr 1fr 1f;
  grid-template-rows: 40vh 30vh 30vh;
  row-gap: 15px;
  column-gap: 15px;
  grid-template-areas:
    "main main main main"
    "obj1 obj1 obj2 obj2"
    "obj3 obj4 obj5 obj6";
}

.container #main {
  grid-area: main;
  display: flex;
  flex-direction: column;
  border-radius: 15px;
  padding: 5px;
  background-color: var(--content-background);
  align-items: center;
  justify-content: center;
}

.container #obj1 {
  grid-area: obj1;
  display: flex;
  flex-direction: column;
  border-radius: 15px;
  padding: 5px;
  background-color: var(--content-background);
  align-items: center;
  justify-content: center;
}

