@media screen and (max-width: 500px) {
  .container {
    height: auto;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: repeat(5, 1fr);
    row-gap: 15px;
    column-gap: 15px;
    grid-template-areas:
      "main main "
      "obj1 obj1"
      "obj2 obj2"
      "obj3 obj4"
      "obj5 obj6";
  }
}
